// Filters.js
import React, { useState } from "react";
import { useTranslation } from "react-i18next";

const Filters = ({ onSelectFilter }) => {
  const { t } = useTranslation();
  const [selectedFilters, setSelectedFilters] = useState([]);

  const handleFilterClick = (filter) => {
    if (filter === "all") {
      //   
      setSelectedFilters([]);
      onSelectFilter([]);
    } else {
      setSelectedFilters([filter]);
      onSelectFilter([filter]);
    }
  };

  return (
    <div className="filters">
      <button
        className={selectedFilters.length === 0 ? "filter-clicked" : ""}
        onClick={() => handleFilterClick("all")}
      >
        {t("filter-all-articles")}
      </button>

      <button
        className={selectedFilters.includes("CRM") ? "filter-clicked" : ""}
        onClick={() => handleFilterClick("CRM")}
      >
        CRM
      </button>

      <button
        className={selectedFilters.includes(" ") ? "filter-clicked" : ""}
        onClick={() => handleFilterClick(" ")}
      >
        {t("filter-customer-success")}
      </button>

      <button
        className={selectedFilters.includes("") ? "filter-clicked" : ""}
        onClick={() => handleFilterClick("")}
      >
        {t("filter-sales")}
      </button>

      <button
        className={selectedFilters.includes(" ") ? "filter-clicked" : ""}
        onClick={() => handleFilterClick(" ")}
      >
        {t("filter-customer-management")}
      </button>

      <button
        className={selectedFilters.includes("") ? "filter-clicked" : ""}
        onClick={() => handleFilterClick("")}
      >
        {t("filter-marketing")}
      </button>

      <button
        className={selectedFilters.includes("") ? "filter-clicked" : ""}
        onClick={() => handleFilterClick("")}
      >
        {t("filter-service")}
      </button>
    </div>
  );
};

export default Filters;
